
#long_quiz_box{
    width: 100%;
    height: 300px;
    margin-top: 100px;
    #credit{
        margin-top: 30px;
    }
    #answer, #answer2{
        width: 90%;
        height: 50px;
        font-size: larger;
        text-align:center;
    }
    #menu_btn{
        height: 40px;
        width: 80px;
        margin: 20px auto;
        display: block;
    }
 #restart_btn{
    height: 40px;
    width: 80px;
    margin: 20px auto;
    display: none;
}

#strings_select,#mode_select{
    display: inline;
}

#quiz{
    margin-top: 60px;
    text-align:center;

    font-size: 30px;
}
#next_quiz{
    
    text-align:center;

    color: rgba(240, 248, 255, 0.494);
    margin-top: 10px;

    height: 20px;
    font-size: 20px;

}
#strings,#level{
    width: 150px;
}
#mode,#strings,#level,#q_type,label{
    font-size: 20px;
    
}

label{
    margin-right: 5px;
}
#long, #shot,#mode_select{

    margin-left: 10px;

    display: inline-block;
}

#debug_sys{
    right: 30px;
    display: inline-block;
    position: absolute;

}

#debug{

    width: 16px;
    height: 16px;
}


#answer, #answer2{
    margin-top: 10px;
}

#setting{
    left: 10px;
    top:0px;
    position: relative;
    text-align: left;
}
option{
    text-align:center;

}

}